import React, {useState, useEffect} from 'react'
import firebase from './firebase'
import { navigate } from '@reach/router'
import parse  from 'html-react-parser'
import '@reach/router'
import './ProjectsAccordion.css'
import Collapse from 'rc-collapse'

let Panel = Collapse.Panel

const ProjectsAccordion = (props) => {
    const [projects, setProjects] = useState([])
    const [id, setId] = useState()

    useEffect(() => {
        firebase
            .firestore()
            .collection('projects')
            .orderBy('year', 'desc')
            .onSnapshot(snapshot => {
                setId(snapshot.docs[0].id)
                setProjects(snapshot.docs)
            })
    }, [])

    const addProject = () => {
        firebase.firestore().collection('projects').add({
            title: '',
            year:2020,
            description: '',
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        }).then(ref => {
            navigate('/edit/' + ref.id)
        }).catch( error => console.log(error))
    } 

    const updateId = i => {
        console.log(i)
        if(projects){
            setId(projects[i].id)
        }
    }
 
    return (
        <main className='accordion'>
        <div className='menu'>
            <Collapse accordion={true} destroyInactivePanel={true}>
                <Panel transitionAppear={true} header="hello" headerClass="my-header-class">this is panel content</Panel>
                <Panel header="title2">this is panel content2 or other</Panel>
            </Collapse>
            </div>

        {
            projects.length > 0 
        ? 
        projects.map(
            (doc, index) => 
                <div className='project-acc' key={doc.id}>
                    <h1>{doc.data().title}</h1>
                    <div>{ parse(doc.data().description) }</div>
                </div>
            )
        :
            <p>Getting projects, hold on...</p>
        }
        </main>

    )
}

export default ProjectsAccordion
